import * as React from 'react';

import './SearchBox.scss';

export interface ISearchBoxProps {
  label?: string;
  onChange?: (value: string) => void;
  /* tslint:disable:no-any */
  onSearch?: (e: any) => void;
  /* tslint:enable:no-any */
}

export interface ISearchBoxState {
  isActive: boolean;
  value: string;
}

export default class SearchBox extends React.Component<ISearchBoxProps, ISearchBoxState> {
  public state: ISearchBoxState;

  constructor() {
    super();

    this.state = {
      isActive: false,
      value: ''
    };

    this._onClose = this._onClose.bind(this);
    this._onInputBlur = this._onInputBlur.bind(this);
    this._onInputChange = this._onInputChange.bind(this);
    this._onInputFocus = this._onInputFocus.bind(this);
    this._onInputKeyPress = this._onInputKeyPress.bind(this);
  }

  public render(): React.ReactElement<ISearchBoxProps> {
    const {
      label
    }: ISearchBoxProps = this.props;

    return (
      <div className={ 'ms-SearchBox' + (this.state.isActive ? ' is-active' : '') }>
        <input className='ms-SearchBox-field'
          onBlur={ this._onInputBlur }
          onChange={this._onInputChange }
          onFocus={this._onInputFocus }
          onKeyPress={this._onInputKeyPress }
          value={this.state.value} />
        <label className='ms-SearchBox-label' style={{
          display: this.state.isActive || this.state.value ? 'none' : 'block'
        }}>
          <i className='ms-SearchBox-icon ms-Icon ms-Icon--search' />
          {label}
        </label>
        <button className='ms-SearchBox-closeButton' onMouseDown={ this._onClose }>
          <i className='ms-Icon ms-Icon--x' />
        </button>
      </div>
    );
  }

  public getValue(): string {
    return this.state.value;
  }

  public setValue(value: string): void {
    this.setState({
      isActive: this.state.isActive,
      value: value
    });
  };

  private _onClose(): void {
    this.setState({
      isActive: false,
      value: ''
    });
  }

  private _onInputBlur(): void {
    this.setState({
      isActive: false,
      value: this.state.value
    });
  }

  private _onInputChange(e: React.FormEvent): void {
    this.setState({
      isActive: this.state.isActive,
      value: (e.target as HTMLInputElement).value
    });

    if (this.props.onChange) {
      this.props.onChange((e.target as HTMLInputElement).value);
    }
  }

  private _onInputFocus(): void {
    this.setState({
      isActive: true,
      value: this.state.value
    });
  }

  private _onInputKeyPress(e: React.KeyboardEvent): void {
    const value: string = this.getValue();

    if (value && e.charCode === 13 /* Enter */ && this.props.onSearch) {
      this.props.onSearch(this.getValue());
    }
  }

}
